import React, { useEffect, useState } from 'react';
import { BrowserRouter, Switch, Route, Link } from 'react-router-dom';

import './App.css';

import LogoReact from './assets/img/proa.svg';
import LogoReact2 from './assets/img/proa2.svg';
import MenuImg from './assets/img/menu.png';
import MenuImg2 from './assets/img/menu2.png';

import Main from './assets/pages/Home';

import {

  Header,
  Menu,
  MenuLista,
  ListaItem,
  ButtonLista,
  Menu2,
  MenuLista2,
  ListaItem2,
  ButtonLista2,

} from './assets/styles/app';

function App(){

  const [offset, setOffset] = useState(0);  
  
    useEffect(() => {
      window.onscroll = () => {
        setOffset(window.pageYOffset)
      }
    }, []);

  return(

    <BrowserRouter basename={process.env.PUBLIC_URL}>
  
      <Header>

        {offset == 0 &&

          <Menu>

            <MenuLista>

              <ListaItem>

                <img src={LogoReact} />

              </ListaItem>
              
              <ListaItem>

                <Link className="links" to="/">Início</Link>

              </ListaItem>
              
              <ListaItem>

                <Link className="links" to="/seja-aluno-proa">Seja Aluno PROA</Link>

              </ListaItem>
              
              <ListaItem>

                <Link className="links" to="/quem-somos">Quem Somos</Link>

              </ListaItem>
              
              <ListaItem>

                <Link className="links" to="/como-apoiar">Como Apoiar</Link>

              </ListaItem>
              
              <ListaItem>

                <Link className="links" to="/resultados">Resultados</Link>

              </ListaItem>

              <ListaItem>

                <Link className="links" to="/mais">Mais <img className="menuimg" src={MenuImg} /></Link>

              </ListaItem>

              <ListaItem>

                <ButtonLista>ENTRAR</ButtonLista>

              </ListaItem>
              
            </MenuLista>

          </Menu>

        }

        {offset != 0 &&

          <Menu2>

            <MenuLista2>

              <ListaItem2>

                <img src={LogoReact2} />

              </ListaItem2>
              
              <ListaItem2>

                <Link className="links2" to="/">Início</Link>

              </ListaItem2>
              
              <ListaItem2>

                <Link className="links2" to="/seja-aluno-proa">Seja Aluno PROA</Link>

              </ListaItem2>
              
              <ListaItem2>

                <Link className="links2" to="/quem-somos">Quem Somos</Link>

              </ListaItem2>
              
              <ListaItem2>

                <Link className="links2" to="/como-apoiar">Como Apoiar</Link>

              </ListaItem2>
              
              <ListaItem2>

                <Link className="links2" to="/resultados">Resultados</Link>

              </ListaItem2>

              <ListaItem2>

                <Link className="links2" to="/mais">Mais <img className="menuimg" src={MenuImg2} /></Link>

              </ListaItem2>

              <ListaItem2>

                <ButtonLista2>ENTRAR</ButtonLista2>

              </ListaItem2>
              
            </MenuLista2>

          </Menu2>

        }

      </Header>

      <Switch>

        <Route exact path="/">

          <Main />

        </Route>

      </Switch>
  
    </BrowserRouter>

  );

}

export default App;