import styled from 'styled-components';

export const Container = styled.div`
    display: flex;
    flex-direction: column;
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
`;

export const MenuTitle = styled.div`
    background-color: #012060;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding-bottom: 20px;
`;

export const MenuTitleSubText = styled.p`
    color: #fff;
    margin: 0;
    font-size: 24px;
    display: flex;
    justify-content: center;
    align-items: center;
`;

export const MenuTitleText = styled.h1`
    color: #fff;
    margin: 0;
    font-size: 90px;
    display: flex;
    justify-content: center;
    align-items: center;
`;

export const MenuTitleSpan = styled.p`
    color: #fff;
    margin: 0;
    font-size: 15px;
    font-weight: bold;
    cursor: pointer;
    display: flex;
    justify-content: center;
    align-items: center;
    padding-left: 10px;

    :hover{
        color: #2dc6ff;
    }
`;

export const Noticias = styled.main`
    background: linear-gradient(to bottom, #01aff0 50%, #fff 50%);
    display: flex;
    height: 465px;
    flex-direction: column;
    align-items: center;
    padding-top: 4px;
`;

export const Search = styled.input`
    width: 540px;
    height: 50px;
    font-size: 25px;
    border: 0;
    outline: none;
    border-radius: 5px;    
    background-image: url(${props => props.img});
    background-repeat: no-repeat;
    background-size: 30px;
    background-position-y: center;
    background-position-x: 15px;
    padding: 10px 0 10px 60px;
    color: #004282;
    background-color: #5ccbf5;
    transition: all ease .2s;

    :focus, :hover{
        background-color: #99dff9;
    }
`;

export const AreaNoticias = styled.div`
    display: flex;
    position: relative;
    align-items: center;
    justify-content: center;
    padding-top: 30px;
`;

export const NoticiaItem = styled.div`
    min-width: 300px;
    max-width: 320px;
    min-height: 300px;    
    flex: 1;
    position: relative;
    box-shadow: 0 0 25px grey;
    border-radius: 5px;
    border: 1px solid lightgrey;
    cursor: pointer;
`;

export const NoticiaImg = styled.div`
    width: 320px;
    height: 150px;
    background-image: url(${props => props.img});
    background-size: 100%;
    background-repeat: no-repeat;
    background-position-y: top;
    position: relative;
    border-radius: 5px;

    ::after{

        content: "";
        background-image: url(${props => props.play});
        width: 100%;
        height: 100%;
        position: absolute;
        background-repeat: no-repeat;
        background-position: center;
    }

`;

export const NoticiaText = styled.div`
    width: 320px;
    height: 150px;
    background-color: #fff;
`;

export const NoticiaCategoria = styled.h3`
    color: #fca517;
    margin: 0;
    text-align: center;
    padding: 10px 0;
    font-size: 15px;
`;

export const NoticiaDesc = styled.p`
    color: #30313c;
    margin: 0;
    text-align: center;
    padding-top: 10px;
    font-size: 18px;
    font-weight: bold;
    padding: 0 20px 10px 20px;
`;

export const Section = styled.section`
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    width: 90vw;
    align-self: center;
`;

export const Article = styled.article`
    background-image: url(${props => props.img});
    background-repeat: no-repeat;
    width: ${props => props.size == 1?"30%":"66%"};
    height: 300px;
    background-size: cover;
    display: flex;
    margin: 20px 0;
    box-shadow: 0 0 30px #777;
    position: relative;
    cursor: pointer;

    ::after{
        content: "${props => props.frase}";
        position: absolute;
        width: 100%;
        height: 50%;
        bottom: 0;
        background: linear-gradient(to bottom, rgba(0,0,0,0), rgba(0,0,0,1));
        color: #fff;        
        font-weight: bold;
        display: ${props=>props.img?"flex":"none"};
        justify-content: flex-start;
        align-items: flex-end;
        font-size: 30px;
        padding-left: 20px;
        padding-bottom: 20px;
        box-sizing: border-box;
    }

    ::before{
        content: "${props => props.categoria}";
        position: absolute;
        width: 90px;
        height: 20px;
        background-color: ${props => props.color};
        padding: 5px 20px;
        text-align: center;
        color: #fff;
        font-weight: bold;
        font-size: 15px;
        letter-spacing: 1.5px;
        bottom: 35%;
        left: 20px;
        z-index: 1;
        display: ${props=>props.img?"block":"none"};
    }

`;

export const ArticleImg = styled.div`
    flex: 1;
    background-image: url(${props => props.img});
    background-repeat: no-repeat;
    background-size: cover;
`;

export const ArticleText = styled.p`
    flex: 1;
    padding-left: 30px;
    font-weight: bold;
    font-size: 20px;
`;

export const ArticleCat = styled.p`
    width: 90px;
    height: 20px;
    background-color: ${props => props.color};
    padding: 5px 20px;
    margin: 0;
    text-align: center;
    color: #fff;
    font-weight: bold;
    font-size: 15px;
    letter-spacing: 1.5px;
    margin-bottom: 20px;
`;

export const PartnerArea = styled.div`
    display: flex;
    margin-top: 30px;
    align-items: center;
    flex-direction: column;
`;

export const Line = styled.p`
    margin: 0;
    font-size: 20px;
    position: relative;
    display: flex;
    justify-content: center;
    background-color: #fff;
    padding: 0 30px;
    color: #30313c;
    font-weight: bold;
    margin-bottom: 30px;

    ::before{
        content: " ";
        position: absolute;
        top: 10px;
        width: 90vw;
        background-color: #ccc;
        height: 3px;
        z-index: -1;
    }

`;

export const PartnerItem = styled.div`
    min-width: 100px;
    max-width: 120px;
    min-height: 100px;    
    flex: 1;
    position: relative;
    box-shadow: 0 0 15px grey;
    border-radius: 5px;
    border: 1px solid lightgrey;
    padding: 20px;
`;

export const PartnerImg = styled.div`
    width: 120px;
    height: 100px;
    background-image: url(${props => props.img});
    background-size: contain;
    background-repeat: no-repeat;
    background-position-y: center;
    position: relative;
    border-radius: 5px;
    cursor: pointer;
    transition: all ease .2s;

    ::after{
        transition: all ease .2s;
        content: " ";
        color: #fff;
        font-size: 20px;
        background-color: rgba(0, 0, 0, 0);
        position: absolute;
        width: calc(100% + 40px);
        height: calc(100% + 40px);
        top: -20px;
        left: -20px;
    }

    :hover{
        ::after{
            display: flex;
            justify-content: center;
            align-items: center;
            transition: all ease .2s;
            content: "Financiador";
            color: #fff;
            font-size: 20px;
            background-color: rgba(0, 0, 0, 0.8);
            position: absolute;
            width: calc(100% + 40px);
            height: calc(100% + 40px);
            top: -20px;
            left: -20px;
        }
    }
`;

export const PartnerLink = styled.p`
    color: #00b8f2;
    cursor: pointer;
    font-size: 20px;
    font-weight: bold;
    padding-top: 10px;
    padding-bottom: 30px;
`;

export const Footer = styled.div`
    display: flex;
    background: url(${props => props.img}) center bottom / cover no-repeat rgb(0, 66, 130);
    z-index: 20;
    padding: 50px 0 0 0;
    flex-direction: column;
    height: auto;
`;


export const FooterArea = styled.div`
    display: flex;
`;

export const FooterLogo = styled.img`
    padding-left: 30px;
`;

export const FooterListArea = styled.div`
    display: flex;
    flex: 1;
    flex-wrap: wrap;
    border-right: 1px solid #000;

`;

export const FooterList = styled.ul`
    list-style: none;
    display: flex;
    width: 100%;
    flex-wrap: wrap;
    align-items: center;
`;

export const FooterListItem = styled.li`
    width: 50%;
    color: #fff;
    font-weight: bold;
    padding-bottom: 10px;
    font-size: 18px;
    cursor: pointer;

    :hover{
        color: #2ec6ff;
    }
`;

export const FooterAddressArea = styled.div`
    display: flex;
    flex: 1;
    flex-wrap: wrap;
    border-right: 1px solid #000;
`;

export const FooterAddressList = styled.ul`
    list-style: none;
    display: flex;
    flex-direction: column;
    justify-content: center;
`;

export const FooterAddressListItem = styled.li`
    color: #fff;
    font-weight: bold;
    font-size: 15px;
`;

export const FooterForm = styled.form`
    display: flex;
    flex-direction: column;
    padding: 0 80px;
    position: relative;
`;

export const FooterFormSpan = styled.span`
    color: #fff;
    font-weight: bold;
    font-size: 17px;
    padding-bottom: 5px;
`;

export const FooterFormInputArea = styled.div`
    display: flex;
    align-items: center;
`;

export const FooterFormInput = styled.input`
    background-color: transparent;
    height: 50px;
    border: 1px solid #000000;
    border-radius: 5px;
    width: 200px;
    position: relative;
    display: flex;
    align-items: center;
    outline: none;
    font-size: 15px;
    color: #fff;
    border-top-right-radius: 0;
    border-bottom-right-radius: 0;
    border-right: 0;
`;

export const FooterFormSubmit = styled.button`
    background-color: #2ec6ff;
    color: #000;
    font-weight: bold;
    border-radius: 5px;
    border: 0;
    outline: 0;
    position: relative;
    cursor: pointer;
    height: 30px;

    ::after{
        content: " ";
        border-radius: 5px;
        left: -5px;
        top: -12px;
        position: absolute;
        border: 1px solid #000;
        border-top-left-radius: 0;
        border-bottom-left-radius: 0;
        width: 100%;
        height: 42px;
        padding: 5px 15px 5px 0;
        border-left: 0;
    }    

`;

export const BlackArea = styled.div`
    display: flex;
    background-color: #000;
    padding: 20px 40px;
`;

export const BlackFirst = styled.div`
    flex: 1;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    color: #fff;
`;

export const BlackSecond = styled.div`
    flex: 1;
    display: flex;
    justify-content: flex-end;
    align-items: center;
    color: #fff;
`;

export const SocialImg = styled.img`
    width: 30px;
    height: 30px;
    background-color: #fff;
    border-radius: 50%;
    padding: 2px;
    cursor: pointer;
    margin: 0 5px;

    :first-child{
        margin-left: 30px;
    }
`;

export const Subscribe = styled.div`
    width: 100vw;
    height: 80px;
    position: fixed;
    bottom: 0;
    background-color: #00b8f2;
    display: flex;
    align-items: center;
    justify-content: space-evenly;
    padding: 5px 0;
    z-index: 3;
`;

export const SubscribeText = styled.h2`
    font-size: 30px;
    color: #fff;
    margin: 0;
    padding-right: 40px;
`;

export const SubscribeButton = styled.button`
    font-size: 20px;
    color: #00b8f2;
    border: 0;
    outline: none;
    padding: 20px 40px;
    cursor: pointer;
    font-weight: bold;
`;